import React, {Component} from 'react';
import 'react-table/react-table.css'
import Table from "../../../components/Table/Table";
import {create_UUID, showAxiosError} from "../../../helper/helper";
import {requestK} from "../../../helper/APIUtils";
import {
  renewListForDepartment,
  renewListForPrivileges,
  statusTypeFilterData
} from "../common/PeykHelper";
import DefaultModal from "../../../components/DefaultModal";
import Alert from "react-s-alert";
import DropdownCheck from "../../../components/Table/DropdownCheck";
import DepartmentSelectionTree from "../organization/DepartmentSelectionTree";
import {Col} from "reactstrap";
import {authorizationWays} from "../../../helper/helper";
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";

class SelectDocumentModal extends Component {

  constructor(props) {
    super(props);
    this.state = {
      selectionEmployee: [],
      selectionDoc: [],
      selectedAll: false,
      conditionalSelection: [],
      loading: false,
      manual: false,
      btnDisabledEmployee: true,
      btnDisabledDoc: true,
      content: [],
      docContent: [],
      treeData: [],
      total: 0,
      totalDoc: 0,
      documentExist: false,
      searching: false,
      searchQuery: undefined,
      page: 0,
      size: 10,
      pageDoc: 0,
      sizeDoc: 5,
      detailedDepartment: false,
      selectedAuthorizationWay: authorizationWays.DEPARTMENT
    };
    this.handleSearch = this.handleSearch.bind(this);
    this.fetchData = this.fetchData.bind(this);
    this.fetchDataDoc = this.fetchDataDoc.bind(this);
    this.getTrStyles = this.getTrStyles.bind(this);
    this.setSelectedDepts = this.setSelectedDepts.bind(this);
    this.downloadDocumentsMedia = this.downloadDocumentsMedia.bind(this);
    this.isSelected = this.isSelected.bind(this);
    this.toggleCheckboxChange = this.toggleCheckboxChange.bind(this);
    this.onAuthorizationWaysChange = this.onAuthorizationWaysChange.bind(this);
  }

  componentDidMount() {
    this.setState({conditionalSelection: this.props.conditionalList});
    this.readDepartmentTreeData();
  }

  async onAuthorizationWaysChange(event) {
    let self = this;
    await self.setState({selectedAuthorizationWay: event.target.value});
    self.readDepartmentTreeData();
  }


  readDepartmentTreeData = () => {
    let self = this;
    self.setState({treeData: [],selectedDepts:[]});
    let params = {
      url: '/department/readDepartmentTreeData',
      method: "post",
      data: self.state.selectedAuthorizationWay
    };
    requestK(params)
      .then(function (response) {
        self.setState({treeData: response.data});
        self.props.reload(false);
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  setSelectedDepts(url) {
    let self = this;
    let selectedDepts = self.state.selectedDepts;
    if (selectedDepts) {
      Object.keys(selectedDepts).map(dept => {
        if (selectedDepts[dept]) {
          if (self.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE) {
            url = url + '&expenditureDepartment_id=' + dept;
          } else {
            url = url + '&department_id=' + dept;
          }
        }
      });
    }
    return url;
  }

  fetchData(accessor, direction, page, size) {
    let self = this;
    let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
    let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
    let searchQueryResult = self.createCustomFields(searchQuery);
    let url = '?page=' + page + '&size=' + size + searchQueryResult[0] + sortStr +
      '&customFields=' + searchQueryResult[1];
    url = self.setSelectedDepts(url);
    let params = {
      url: '/employee/readActiveEmployeeAll' + url,
      method: "get",
    };
    self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
    requestK(params)
      .then(function (response) {
        self.setState({
          page: page,
          size: size,
          total: (response.data && response.data.page) ? response.data.page.totalElements : 0,
          content: (response.data && response.data.page) ? response.data.page.content : [],
          totalIdList: response.data ? response.data.selectedIdList : [],
          loading: false,
          searching: false
        });
      })
      .catch(function (error) {
        self.setState({loading: false, searching: false});
        showAxiosError(error);
      });
  }

  fetchDataDoc(accessor, direction, page, size) {
    let self = this;
    let params = {
      url: '/survey/getDocumentsFromSurvey' + '?page=' + page + '&size=' + size + '&docId='+self.props.selected.id,
      method: "get",
    };
    requestK(params)
      .then(function (response) {
        if (response.data.content.length>0){
          self.setState({
            sizeDoc:size,
            pageDoc:page,
            documentExist: response.data.content,
            docContent: response.data.content ? response.data.content : [],
            totalDoc: response.data.content ? response.data.totalElements : 0,
            loading: false,
            searching: false
          });
        }else{
          self.props.close();
          Alert.warning("Bu formun içerisinde indirilebilir doküman bulunmamaktadır.", {
            position: 'top-right',
            effect: 'stackslide',
            timeout: 3000
          });
        }
      }).catch(function (error) {
      showAxiosError(error);
    });
  }

  createCustomFields = (searchQuery) => {
    let customFieldsQuery = "";
    if (searchQuery.length) {
      let searchList = searchQuery.toString().split("&");
      searchQuery = "";
      searchList.map(e => {
        if (e.length) {
          if (e.includes("cstmfld")) {
            customFieldsQuery = customFieldsQuery + (customFieldsQuery.length ? "#" : "") + e.replace("cstmfld", "");
          } else {
            searchQuery = searchQuery + "&" + e;
          }
        }
      });
    }
    return [searchQuery, customFieldsQuery];
  };

  toggleCheckboxChange(e, row, id) {
    let self = this;
    let checked = e.target.checked;
    let items = id==="employee"?self.state.content:self.state.docContent;
    let selection = id==="employee"?self.state.selectionEmployee:self.state.selectionDoc;
    if (row === "all") {
      let selection = [];
      if (checked) {
        items.forEach(item => {
          selection.push(item["id"]);
        });
      }
      self.setState({selection: selection, selectedAll: checked});
      if (id==="employee"){
        self.setState({btnDisabledEmployee: selection.length===0});
      }else {
        self.setState({btnDisabledDoc: selection.length===0});
      }
    } else {
      let index = selection.indexOf(row["id"]);
      if (index >= 0) {
        selection = [
          ...selection.slice(0, index),
          ...selection.slice(index + 1)
        ];
      } else {
        selection.push(row["id"]);
      }
      if (id==="employee"){
        self.setState({
          selectionEmployee: selection,
          selectedAll: ((selection.length > 0) ? (selection.length === self.state.content.length) : false),
          btnDisabledEmployee: selection.length===0
        });
      }else{
        self.setState({
          selectionDoc: selection,
          btnDisabledDoc: selection.length===0
        });
      }
    }
  }

  isSelected(id) {
    return (
      (this.state.selectionEmployee && this.state.selectionEmployee.indexOf(id) !== -1) ||
      (this.state.selectionDoc && this.state.selectionDoc.indexOf(id) !== -1)
    );
  }

  getTrStyles(row) {
    return {
      backgroundColor: (
        ((this.state.selectionEmployee || this.state.selectionDoc) && (this.state.selectionEmployee.indexOf(row.id) !== -1||this.state.selectionDoc.indexOf(row.id) !== -1)) ? "#ffc" : "#f9f9f9"
      )
    };
  }

  arrangeDataSet = (survey) => {
    let dataSet = {};
    dataSet.sheetName = "Doküman_Listesi";
    dataSet.uuid = create_UUID();
    dataSet.survey = survey;
    dataSet.type = 'pdf';
    return dataSet;
  }

  handleSearch(searchQuery) {
    this.setState({
      searchQuery,
      searching: true
    }, () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size))
  }

  downloadDocumentsMedia() {
    let self = this;
    let selectionDoc = self.state.selectionDoc;
    let selectionEmployee = self.state.selectionEmployee;
    let payload = self.arrangeDataSet(self.props.selected);
    let params = {
      url: `/survey/getDocumentsFromEmployee`,
      method: "post",
      data: {
        surveyId: self.props.selected.id,
        selectionDoc: selectionDoc,
        selectionEmployee: selectionEmployee,
        payload: payload
      }
    };
    self.setState({openDocumentModal: false});
    requestK(params)
      .then(function (response) {
        Alert.success("İndirme talebiniz işleme alındı. 'Bekleyen İndirmeler'den' takip edebilirsiniz.", {
          position: 'top-right',
          effect: 'stackslide',
          timeout: 8000
        });
      }).catch(function (error) {
      showAxiosError(error);
    });
  };

  detailedDepartment = () => {
    this.setState({
      detailedDepartment: !this.state.detailedDepartment
    });
  };

  render() {
    let pageIdList = [], dublicateList = [], tempList = [];
    if (this.state.content && this.state.content instanceof Array) {
      this.state.content.map(e => {
        if (!e.name || e.statusType === "ACTIVE") {
          pageIdList.push(e.id);
        }
        if (!tempList.includes(e.tckn)) {
          tempList.push(e.tckn);
        } else if (!dublicateList.includes(e.tckn)) {
          dublicateList.push(e.tckn)
        }
      });
    }

    let columnList = [
      {
        Header: (
          <DropdownCheck selectionIdList={this.state.selectionEmployee}
                         totalIdList={this.state.totalIdList}
                         pageIdList={pageIdList}
                         onChange={e => this.setState({selectionEmployee: e,btnDisabledEmployee:e.length===0})}/>
        ),
        accessor: "id",
        selectType: "checkbox",
        sortable: false,
        Cell: (row, index) => (this.state.content &&
          <input type="checkbox" checked={this.isSelected(row.id)}
                 disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                 onChange={(e) => this.toggleCheckboxChange(e, row, "employee", index)}/>)
      },
      {
        Header: "Ad",
        accessor: "name"
      },
      {
        Header: "Soyad",
        accessor: "surname"
      },
      {
        Header: "TCKN",
        accessor: "tckn"
      },
      {
        Header: "Durum",
        accessor: "statusType",
        Cell: row => (
          <div>
                        <span>{statusTypeFilterData().map(function (e) {
                          if (e.value === row.statusType) return e.label
                        })}</span>
          </div>)
      },
    ];
    let columns = renewListForDepartment(this, columnList, "accessor");

    const fieldList = [
      {
        label: "Adı",
        accessor: "name",
        type: "text",
        operator: "=",
      },
      {
        label: "Soyadı",
        accessor: "surname",
        type: "text",
        operator: "=",
      },
      {
        label: "TCKN",
        accessor: "tckn",
        type: "numericText",
        operator: "=",
      },
      {
        label: "e-Posta Adresi",
        accessor: "email",
        type: "text",
        operator: "=",
      },
      {
        label: "Telefon Numarası",
        accessor: "telephone",
        type: "text",
        operator: "=",
      },
      {
        label: "Durum",
        accessor: "statusType",
        type: "select",
        operator: "=",
        selectData: statusTypeFilterData()
      },
    ];
    let fields = renewListForPrivileges(this, fieldList, "accessor", false);

    const documentColumns = [
      {
        Header: "Seçim",
        accessor: "id",
        type: "checkbox",
        sortable: false,
        Cell: (row, index) => (this.state.docContent &&
          <input type="checkbox" checked={this.isSelected(row.id)}
                 disabled={this.props.selectedList && this.props.selectedList.indexOf(row.id) !== -1}
                 onChange={(e) => this.toggleCheckboxChange(e, row, "doc", index)}/>)
      },
      {
        Header: "Doküman İsmi",
        accessor: "docName",
        sortable: false,
        type: "text",
      }
    ];

    return <DefaultModal disabled={this.state.btnDisabledEmployee || this.state.btnDisabledDoc}
                         isOpen={this.props.isOpen}
                         size="xxl"
                         title="Doküman Seçimi"
                         confirmBtnText="İndirme Talebi"
                         cancelBtnText="Kapat"
                         onConfirm={this.downloadDocumentsMedia}
                         onCancel={() => this.props.close()}>
      <div>
        <div className="row"
             style={{paddingTop: "1%", marginTop: "1%", marginBottom: "1%", paddingLeft: "1.8%", paddingRight: "1.8%"}}>
          {this.state.treeData && this.state.treeData.length > 0 &&
            <div className="col-xs-12 col-md-6" id='departmentContainer' style={{padding: 0,height:"400px"}}
                 onClick={() => this.detailedDepartment()}>
              <Col sm={6}>
                <div  style={{textAlign:'center'}} onChange={this.onAuthorizationWaysChange}>
                  <input sm={6} type="radio" value={authorizationWays.DEPARTMENT}
                         name="authorizationWay"
                         defaultChecked={this.state.selectedAuthorizationWay === authorizationWays.DEPARTMENT}/> Departman
                  <input sm={6} type="radio" value={authorizationWays.EXPENDITURE}
                         name="authorizationWay"
                         defaultChecked={this.state.selectedAuthorizationWay === authorizationWays.EXPENDITURE}
                         style={{marginLeft:20}}
                  /> Masraf Yeri
                </div>
              </Col>
              <DepartmentSelectionTree treeData={this.state.treeData}
                                       selectedDepts={this.state.selectedDepts}
                                       loading={e => this.setState({loading: e})}
                                       onSelect={e => this.setState({selectedDepts: e, manual: true})}
                                       selectedAuthorizationWay = {this.state.selectedAuthorizationWay}
                                       changeOnConditionalList={e => this.setState({conditionalSelection: e})}/>
            </div>}
          {
            <div className="col-xs-12 col-md-6">
              <Table columns={documentColumns}
                     content={this.state.docContent}
                     loading={this.state.loading}
                     page={this.state.pageDoc}
                     size={this.state.sizeDoc}
                     total={this.state.totalDoc}
                     manual={this.state.manual}
                     options={["5", "10", "20"]}
                     fetchData={this.fetchDataDoc}
                     getTrStyles={this.getTrStyles}
              />
              <DetailedSearch fields={fields}
                                loading={this.state.searching}
                                onSearch={this.handleSearch}/>
            </div>
          }
        </div>
        <div className="col-xs-12" style={{marginTop: '20px'}}>
          <Table columns={columns}
                 content={this.state.content}
                 loading={this.state.loading}
                 total={this.state.total}
                 page={this.state.page}
                 manual={this.state.manual}
                 size={this.state.size}
                 options={["5", "10", "20", "50", "100"]}
                 fetchData={this.fetchData}
                 getTrStyles={this.getTrStyles}
          />
        </div>
      </div>
      {this.state.alert}
    </DefaultModal>
  }

}

export default SelectDocumentModal;
