import React, {Component} from 'react';
import {Nav} from 'reactstrap';
import Table from '../../../components/Table/Table';
import DetailedSearch from "../../../components/DetailedSearch/DetailedSearch";
import StickButton from "../../../components/StickButton";
import MiniButton from "../../../components/MiniButton";
import SweetAlertExt from "../../../components/SweetAlertExt";
import {create_UUID, showAxiosError} from "../../../helper/helper";
import {requestK} from "../../../helper/APIUtils";
import Alert from "react-s-alert";
import moment from "moment/moment";
import Swal from "sweetalert2";
import SweetAlert from "react-bootstrap-sweetalert";
import Datetime from "react-datetime";
import Joyride from 'react-joyride';
import SelectDocumentModal from "./SelectDocumentModal";
import TextOverFlowTooltip from "../../../components/TextOverFlowTooltip";
import Select from "react-select";
import makeAnimated from "react-select/animated";

const animatedComponents = makeAnimated();

class FormBuilderPage extends Component {
    statesEnum = {
        ACTIVE: 'AKTİF',
        PASSIVE: 'PASİF',
        APPROVED: 'Onaylandı',
        APPROVEWAITING:'Onay Bekliyor',
        WAITING:'Gönderilmeyi Bekliyor',
        REJECTED: 'Reddedildi',
        SAVED: 'Kaydedildi',
        REFORMATION: 'Yeniden Düzenleme',
        SENT: 'Gönderildi'
    }

    constructor(props) {
        super(props);
        this.state = {
            pageNumber: 0,
            pageSize: 10,
            loading: false,
            manual: false,
            list: [],
            total: 0,
            searching: false,
            searchQuery: undefined,
            page: 0,
            size: 10,
            searchKey: "",
            locale: {
                back: 'Geri',
                close: 'Kapat',
                last: 'Son',
                next: 'İleri',
                skip: 'Atla',
            },
            shouldRun: true,
            stop: false,
            steps: [
                {
                    target: '.baseBackground',
                    content: 'Daha önceden oluşturmuş olduğunuz "Talep" ve "Özlük" formlarını düzenleyebileceğiniz ekran burasıdır.',
                    placement: "center",
                    disableBeacon: 'true'
                },
                {
                    target: '.zocial',
                    content: 'Daha önceden form oluşturmadıysanız "Yeni Oluştur" butonuna tıklayıp Form Oluşturma Ekranına gidebilirsiniz.',
                    placement: "auto",
                    disableBeacon: 'true'
                },
            ],
            run: false,
            groupList: [],
            openExcelModal: false,
            openDocumentModal: false,
            startDate: '',
            endDate: '',
            statusFilter:'',
            row: '',
            survey: [],

        };

        this.fetchData = this.fetchData.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.editClicked = this.editClicked.bind(this);
        this.editClicked2 = this.editClicked2.bind(this);
        this.saveClicked = this.saveClicked.bind(this);
        this.createClicked = this.createClicked.bind(this);
        this.createClicked2 = this.createClicked2.bind(this);
        this.deleteClicked = this.deleteClicked.bind(this);
        this.searchWithKey = this.searchWithKey.bind(this);
        this.downloadMedia = this.downloadMedia.bind(this);
        this.getGroupList = this.getGroupList.bind(this);
        this.importFile = this.importFile.bind(this);
        this.exportFile = this.exportFile.bind(this);
        this.saveImport = this.saveImport.bind(this);
    }

    componentDidMount() {
        this.getGroupList();
    }

    getGroupList() {
        let self = this;
        let params = {
            url: '/employee/getGroups',
            method: "get"
        };
        requestK(params)
            .then(function (response) {
                let groupList = [];
                response.data.map((group) => {
                    groupList.push({value: group.id, label: group.name, group: group})
                });
                self.setState({groupList});
            }).catch(function (error) {
            showAxiosError(error);
        });

    }

    getOptionsFromEnum = (enumObj) => {
        const filteredOptions = Object.keys(enumObj)
            .filter(key => enumObj[key] !== 'AKTİF' && enumObj[key] !== 'PASİF')
            .map(key => ({
                value: key,
                label: enumObj[key],
            }));

        return filteredOptions;
    };

    handleSelectChange = selectedOption => {
        let statusFilter = null;
        if (selectedOption!=null){
            statusFilter=selectedOption.map(item => {
                return item["value"];
            })
            console.log(`statusFilter:`, statusFilter);
        }else{
            statusFilter = null;
        }
        console.log(`Selected:`, selectedOption);
        this.setState({statusFilter: statusFilter})
    };
    handleSearch(searchQuery) {
        this.setState({searchQuery, searching: true},
            () => this.fetchData(this.state.accessor, this.state.direction, 0, this.state.size));
    }

    searchWithKey(e) {
        this.setState({searchKey: e.target.value, manual: true});
    }

    fetchData(accessor, direction, page, size) {
        let self = this;
        let sortStr = accessor !== undefined ? ('&sort=' + accessor + ',' + direction) : '';
        let searchQuery = !self.state.searchQuery ? '' : '&' + self.state.searchQuery;
        let params = {
            url: '/survey/readSurveyFormsAll?page=' + page + '&size=' + size + searchQuery + sortStr,
            method: "get"
        };
        self.setState({loading: true, searching: true, manual: self.manual === true ? false : self.manual});
        self.props.reload(true);
        requestK(params)
            .then(function (response) {
                self.setState({
                    page: page,
                    size: size,
                    total: response.data.totalElements,
                    list: response.data.content,
                    loading: false,
                    searching: false
                });
                self.props.reload(false);
            }).catch(function (error) {
            self.setState({loading: false, searching: false});
            self.props.reload(false);
            showAxiosError(error);
        });
    }

    createClicked() {
        this.props.history.push({pathname: `/${this.props.peykName}/formeditor`, editedForm: false});
    }

    createClicked2() {
        this.props.history.push({pathname: `/${this.props.peykName}/formbuilder`});
    }

    importFile() {
        let input = document.createElement('input');
        input.type = 'file';
        input.onchange = e => {
            let file = e.target.files[0];
            let reader = new FileReader();
            reader.readAsText(file);
            reader.addEventListener("load", () => {
                this.saveImport(JSON.parse(reader.result));
            }, false);
        }
        input.click();
    }

    saveImport(survey) {
        let self = this;
        survey.id = null;
        survey.customer = self.props.currentUser.customerId;
        survey.formTemplate = null;
        survey.surveyAnswerGroups = [];
        let params = {
            url: `/survey/saveSurveyForm`,
            method: "post",
            data: survey
        };
        requestK(params)
            .then(function () {
                Swal({
                    title: 'Başarılı ! ',
                    text: ("Kaydetme işlemi başarıyla gerçekleştirildi."),
                    showCancelButton: true,
                    html: "Kaydetme işlemi başarıyla gerçekleştirildi." +
                        '</br></br><span style="color:red;font-size:20px">*</span>Kaydettiğiniz formu "Form Şablonları" sayfasından  bir şablona bağlayarak gönderime hazır hale getirebilirsiniz.',
                    type: 'success',
                    cancelButtonText: "Kapat",
                    confirmButtonText: 'Form Şablonlarına Git',
                }).then((result) => {
                    if (result.dismiss === 'cancel' || result.dismiss === 'overlay') {
                        self.props.history.push(`/${self.props.peykName}/formbuilderpage`);
                        self.fetchData(undefined, undefined, 0, 10);
                    } else {
                        self.props.history.push(`/${self.props.peykName}/formtemplate`);
                        document.getElementById('formbuilderpageNavigator').classList.remove('active');
                        document.getElementById('formtemplateNavigator').classList.add('active');
                    }
                });
            }).catch(function (error) {
            showAxiosError(error);
        });
    }

    exportFile(row) {
        let self = this;
        const jsonData = `data:text/json;chatset=utf-8,${encodeURIComponent(JSON.stringify(row))}`;
        const link = document.createElement("a");
        link.href = jsonData;
        link.download = self.props.currentUser.formattedName + " " + row.name + " formu aktarım dosyası.json";
        link.click();
    }

    editClicked(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.props.history.push({
            pathname: `/${this.props.peykName}/formeditor`,
            selectedSurveyForm: row,
            editedForm: true
        });
    }

    editClicked2(row) {
        let clone = Object.assign({}, row);
        delete clone["_$visited"];
        this.props.history.push({pathname: `/${this.props.peykName}/formbuilder`, selectedSurveyForm: row});
    }

    // editHandleInputChange(event) {
    //     let self = this;
    //     const target = event.target;
    //     let value = target.value;
    //     let name = target.name;
    //     let newRole = Object.assign({}, this.state.newRole);
    //     newRole[name] = value;
    //     self.setState({newRole});
    // }

    deleteClicked(row) {
        let self = this;
        let params = {
            url: `/survey/deleteSurveyForm`,
            method: "post",
            data: row
        };
        self.setState({disabledDeleteBtn: true});
        requestK(params)
            .then(function () {
                self.setState({
                    selected: null,
                    manual: true,
                    alert: null,
                    disabledDeleteBtn: false
                });
                Swal({
                    title: 'Başarılı ! ',
                    text: ("Silme işlemi başarıyla gerçekleştirildi."),
                    type: 'success',
                    confirmButtonText: 'Tamam'
                }).then(() => {
                    //self.fetchData();
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledDeleteBtn: false});
        });
    }

    downloadDocuments = (row) => {
        this.downloadMedia(row);
    };
    arrangeDataSet = (row) => {
        let dataSet = {};
        dataSet.sheetName = "Cevap_Listesi";
        dataSet.uuid = create_UUID();
        dataSet.type = 'xlsx';
        dataSet.startDate = this.state.startDate;
        dataSet.endDate = this.state.endDate;
        dataSet.statusFilter = this.state.statusFilter;
        dataSet.survey = row;
        return dataSet;
    };
    downloadMedia = (row) => {
        let self = this;
        let data = self.arrangeDataSet(row);
        if (moment(self.state.startDate).isValid() &&
            moment(self.state.endDate).isValid() &&
            moment(self.state.startDate).startOf('day').isBefore(moment(self.state.endDate).startOf('day'))) {
            let params = {
                url: `/survey/getAnswers`,
                method: "post",
                data: data
            };
            self.setState({openExcelModal: false});
            requestK(params)
                .then(function () {
                    Alert.success("İndirme talebiniz işleme alındı. 'Bekleyen İndirmeler'den' takip edebilirsiniz.", {
                        position: 'top-right',
                        effect: 'stackslide',
                        timeout: 8000
                    });
                }).catch(function (error) {
                showAxiosError(error);
            });
        } else {
            Alert.warning("Başlangıç ve bitiş tarihleri doldurulmalıdır. Seçilen başlangıç tarihi bitiş tarihinden en az 1 (bir) gün önce olmalıdır.", {
                position: 'top-right',
                effect: 'stackslide',
                timeout: 15000
            });
        }
    };

    saveClicked() {
        let formParameter = Object.assign({}, this.state.selected);
        let self = this;
        let params = {
            url: `/survey/saveFormParameter`,
            method: "post",
            data: formParameter
        };
        self.setState({disabledSaveBtn: true});
        requestK(params)
            .then(function (response) {
                let message = response.data;
                self.setState({
                    selected: null,
                    displayCreateUpdateDialog: false,
                    manual: true,
                    disabledSaveBtn: false
                });
                Alert.success(message, {
                    position: 'top-right',
                    effect: 'stackslide',
                    timeout: 5000
                });
            }).catch(function (error) {
            showAxiosError(error);
            self.setState({disabledSaveBtn: false});
        });
    }

    showExcelModal = () => {
        const optionHeight = 35;
        const maxMenuHeight = 4 * optionHeight;

        return (
            <SweetAlert
                showCancel
                style={{width: '578px', height: '490px'}}
                title="Cevap Listesi"
                confirmBtnBsStyle="info"
                cancelBtnBsStyle="default"
                confirmBtnText='Cevap Listesi İndir'
                disabled={this.state.startDate === '' || this.state.endDate === ''}
                cancelBtnText='Kapat'
                onCancel={() => this.setState({openExcelModal: false, startDate: '', endDate: '', row: ''})}
                onConfirm={() => this.downloadDocuments(this.state.row)}
            >
                <div className="row">
                    <div className='col-sm-12'>
                        <div className='row'>
                            <span style={{fontSize: 'small', margin: '-10px 10px 0 10px'}}>Seçili talep formunun seçilecek zaman aralığındaki cevap listesinin geçmiş kayıt belgesini indirebilirsiniz.</span>
                        </div>
                        <div className='col-sm-12' style={{margin: '1em 0 1em 0'}}>
                            <div className='row'>
                                <div className="col-sm-6">
                                    <span style={{fontWeight: 'bold'}}>Başlangıç Tarihi *</span>
                                </div>
                                <div className="col-sm-6">
                                    <Datetime locale="tr"
                                              className="datepicker"
                                              style={{fontSize: '12px'}}
                                              input={true}
                                              inputProps={{id: 'startDate'}}
                                              closeOnSelect={true}
                                              value={moment(this.state.startDate)}
                                              dateFormat="DD-MM-YYYY"
                                              timeFormat={false}
                                              isValidDate={function (current) {
                                                  return current.isBefore(Datetime.moment());
                                              }}
                                              onChange={(e) => {
                                                  this.setState({startDate: moment(e).format('YYYY-MM-DD')});
                                              }}/>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-12'>
                            <div className='row'>
                                <div className="col-sm-6">
                                    <span style={{fontWeight: 'bold'}}>Bitiş Tarihi *</span>
                                </div>
                                <div className="col-sm-6">
                                    <Datetime locale="tr"
                                              className="datepicker"
                                              input={true}
                                              inputProps={{id: 'startDate'}}
                                              closeOnSelect={true}
                                              value={moment(this.state.endDate)}
                                              dateFormat="DD-MM-YYYY"
                                              timeFormat={false}
                                              isValidDate={function (current) {
                                                  return current.isBefore(Datetime.moment());
                                              }}
                                              onChange={(e) => {
                                                  this.setState({endDate: moment(e).format('YYYY-MM-DD')});
                                              }}/>
                                </div>
                            </div>
                        </div>
                        <div className='col-sm-12' style={{ marginTop: '10px'}}>
                            <div className='row'>
                                <div className="col-sm-6" >
                                    <span style={{fontWeight: 'bold'}}>Durum *</span>
                                </div>
                                <div className="col-sm-6" style={{ fontSize: '15px' }}>
                                    <Select
                                        isMulti
                                        closeMenuOnSelect={false}
                                        components={animatedComponents}
                                        options={this.getOptionsFromEnum(this.statesEnum)}
                                        onChange={this.handleSelectChange}
                                        placeholder="Durum Seçiniz"
                                        styles={{
                                            menu: provided => ({
                                                ...provided,
                                                maxHeight: `${maxMenuHeight}px`,
                                            }),
                                            menuList: provided => ({
                                                ...provided,
                                                maxHeight: `${maxMenuHeight}px`,
                                                overflow: 'auto'
                                            }),
                                        }}
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </SweetAlert>

        )
    }

    showDocumentModal = () => {
        return (
            <SelectDocumentModal
                isOpen={this.state.openDocumentModal}
                selected={this.state.row}
                close={() => this.setState({openDocumentModal: false})}
            />
        )
    }

    activateJoyride = () => {
        this.setState({
            run: !this.state.run
        })
    }

    callback = (data) => {
        if (data.action === 'close' || data.action === 'skip' || data.type === 'tour:end') {
            this.setState({
                run: false
            })
        }
    }


    render() {
        let columns = [
            {
                Header: "Talep Form Adı",
                accessor: "name",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.name} maxLength={60}/></div>)
            },
            {
                Header: "İlişkili Form Şablonu",
                accessor: "formTemplate_formName",
                Cell: row => (<div style={{margin: 0}}><TextOverFlowTooltip text={row.formTemplate?.formName} maxLength={60}/></div>)
            },
            {
                Header: "Oluşturulma Zamanı",
                accessor: "createdAt",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.createdAt).isValid() ? moment(row.createdAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Güncellenme Zamanı",
                accessor: "updatedAt",
                Cell: row => (
                    <div style={{margin: 0}}>
                        {moment(row.updatedAt).isValid() ? moment(row.updatedAt).format("DD-MM-YYYY HH:mm:ss") : ''}
                    </div>
                )
            },
            {
                Header: "Durum",
                accessor: "state",
                Cell: row => (
                    <div>
                        {row.state === this.statesEnum.ACTIVE ?
                            <h6 className="text-success">{this.statesEnum.ACTIVE}</h6> :
                            row.state === this.statesEnum.PASSIVE ?
                                <h6 className="text-danger">{this.statesEnum.PASSIVE}</h6> : ''
                        }
                    </div>
                )
            },
            {
                Header: "İşlemler",
                accessor: "",
                sortable: false,
                Cell: row => (
                    <div style={{textAlign: 'left'}}>
                        <MiniButton title="Düzenle" faIcon="fa-edit" onClick={() => {
                            if (!!row.bulkJson) {
                                this.editClicked(row);
                            } else {
                                this.editClicked2(row);
                            }
                        }}
                                    color="success"/>
                        <MiniButton title="Sil" faIcon="fa-trash" color="danger"
                                    onClick={() => {
                                        this.setState({
                                            alert: (
                                                <SweetAlertExt showCancel warning
                                                               confirmBtnText="Evet"
                                                               cancelBtnText="Hayır"
                                                               confirmBtnBsStyle="danger"
                                                               cancelBtnBsStyle="default"
                                                               title="Emin misiniz?"
                                                               onConfirm={() => this.deleteClicked(row)}
                                                               onCancel={() => this.setState({alert: null})}
                                                               disabled={this.state.disabledDeleteBtn}
                                                >
                                                    {row.name + " silinecek."}
                                                </SweetAlertExt>
                                            )
                                        });
                                    }}/>
                        {(this.props.currentUser.impersonate || this.props.isSystemAccount) &&
                            <MiniButton title="Dışarı Aktar" faIcon="fa-file-export" color="info"
                                        onClick={() => {
                                            this.exportFile(row);
                                        }}/>
                        }
                    </div>
                )
            },
            {
                Header: "Raporlar",
                accessor: "",
                sortable: false,
                Cell: row => (
                    <div style={{textAlign: 'center'}}>
                        <MiniButton title="Excel" faIcon="fa-file-excel-o"
                                    onClick={() => this.setState({openExcelModal: true, row})}
                                    color="success"/>
                        {
                            <MiniButton title="Doküman" faIcon="fa-files-o"
                                        onClick={() => this.setState({openDocumentModal: true, row})}
                                        color="info"/>}
                    </div>
                )
            },
        ];

        const fields = [
            {
                label: "Talep Form Adı",
                accessor: "name",
                type: "text",
                operator: "=",
            },
            {
                label: "İlgili Grup",
                accessor: "surveyAnswerGroups.group.id",
                type: "select",
                operator: "=",
                selectData: this.state.groupList
            }
        ];

        return (
            <div className="row baseBackground">
                <Joyride
                    run={this.state.run}
                    steps={this.state.steps}
                    locale={this.state.locale}
                    continuous={true}
                    showProgress={true}
                    showSkipButton={true}
                    disableScrollParentFix={true}
                    disableScrolling={true}
                    callback={this.callback}
                    styles={{
                        options: {
                            zIndex: 100000,
                            primaryColor: "#50a8e2"   // "#fc9636"
                        }
                    }}
                />
                <Nav className="topbar" style={{paddingLeft: '14px', height: '53px'}}>
                    <StickButton position="leftSide" className="zocial Yeni" label="Yeni Oluştur"
                                 title="Yeni Talep Form Oluştur"
                                 disabled={this.state.disabledSaveBtn} style={{fontSize: 'x-small'}}
                                 onClick={this.createClicked}/>
                    {this.props.isSystemAccount &&
                        <StickButton position="leftSide" className="zocial Yeni" label="Yeni Oluştur2"
                                     title="Yeni Talep Form Oluştur"
                                     disabled={this.state.disabledSaveBtn} style={{fontSize: 'x-small'}}
                                     onClick={this.createClicked2}/>}
                    {(this.props.currentUser.impersonate || this.props.isSystemAccount) &&
                        <StickButton position="leftSide" className="zocial Import" label="İçeri Aktar"
                                     title="İçeri aktar"
                                     style={{fontSize: 'x-small'}}
                                     onClick={this.importFile}/>
                    }

                    <div className="helpButtonContainer" position='rightSide'
                         style={{position: "inherit", right: "4.4em", marginTop: "1.4em"}}>
                        <button className="helpButton" onClick={() => {
                            this.activateJoyride()
                        }}>
                            <span className="tooltipText">Size yardımcı olabilirim</span>
                            <i className="far fa-question-circle helpButtonIcon"/>
                        </button>
                    </div>

                </Nav>
                <div className="col-sm-12 satirBasi">
                    <div className="card-header ">
                        <h4 className="card-title">Talep Formları</h4>
                    </div>
                    <div className="card-body" style={{paddingBottom: '0px'}}>
                        <div className="">
                            <div className="" style={{marginTop: '10px', marginBottom: '0px'}}>
                                <DetailedSearch fields={fields}
                                                loading={this.state.loading}
                                                onSearch={this.handleSearch}/>
                            </div>
                        </div>
                    </div>
                    <div className="card-body">
                        <Table columns={columns}
                               content={this.state.list}
                               loading={this.state.loading}
                               total={this.state.total}
                               page={this.state.page}
                               manual={this.state.manual}
                               size={this.state.size}
                               options={["5", "10", "20", "50", "100"]}
                               fetchData={this.fetchData}
                        />
                    </div>
                </div>
                {this.state.openExcelModal && this.showExcelModal()}
                {this.state.openDocumentModal && this.showDocumentModal()}
                {this.state.alert}
            </div>
        );
    }
}

export default FormBuilderPage;