import React, {Component} from 'react';
import Tree from "rc-tree";
import TreeNode from "rc-tree/es/TreeNode";
import {requestK} from "../../../helper/APIUtils";
import {showAxiosError} from "../../../helper/helper";
import {findChildrenLowestLevel, searchTreeChildrenNameList} from "../common/PeykHelper";
import {Col} from "reactstrap";
import {authorizationWays} from "../../../helper/helper";
class DepartmentSelectionTree extends Component {

    constructor(props) {
        super(props);
        this.state = {
            selectedDepts: {},
            conditionalList: [],
            declineList: [],
        };

        [
            'onSelectionChange',
            'conditionalListChange',
            'declineListChange',
        ].forEach((name) => (this[name] = this[name].bind(this)));
    }

    componentDidMount() {
        let levelInfo = findChildrenLowestLevel(this.props.treeData[0]);
        this.setState({level: levelInfo[0], maxLength: levelInfo[1] + 20});
    }

    onSelectionChange(node, e) {
        let selectedDepts = this.state.selectedDepts;
        let checked = e.target.checked;
        selectedDepts[node.key] = checked;

        let childrenList = searchTreeChildrenNameList(this.props.treeData[0], node.key);
        childrenList && childrenList.map(key => selectedDepts[key] = checked);

        this.setState({selectedDepts});
        this.props.onSelect(selectedDepts);
    }

    conditionalListChange(node, e) {
        let self = this;
        let checked = e.target.checked;
        let deptList = [];
        deptList.push(node.title);
        let params = {
            url: '/employee/getEmployeeListForDept',
            method: "post",
            data: deptList
        };
        self.props.loading(true);
        requestK(params)
            .then(function (response) {
                let conditionalList = self.state.conditionalList;
                if (checked) {
                    if (response.data && Array.isArray(response.data)) {
                        response.data.map(id => conditionalList.push(id));
                    } else if (response.data && typeof response.data === 'number') {
                        conditionalList.push(response.data);
                    }
                } else {
                    if (response.data && Array.isArray(response.data)) {
                        response.data.map(id => {
                            let index = conditionalList.indexOf(id);
                            if (index > -1) {
                                conditionalList.splice(index, 1);
                            }
                        });
                    } else if (response.data && typeof response.data === 'number') {
                        let index = conditionalList.indexOf(response.data);
                        if (index > -1) {
                            conditionalList.splice(index, 1);
                        }
                    }
                }
                conditionalList = Array.from(new Set(conditionalList));
                self.setState({conditionalList});
                self.props.changeOnConditionalList(conditionalList);
                self.props.loading(false);
            }).catch(function (error) {
            showAxiosError(error);
            self.props.loading(false);
        });
    }

    declineListChange(node, e) {
        let self = this;
        let checked = e.target.checked;
        let deptList = [];
        deptList.push(node.title);
        let params = {
            url: '/employee/getEmployeeListForDept',
            method: "post",
            data: deptList
        };
        self.props.loading(true);
        requestK(params)
            .then(function (response) {
                let declineList = self.state.declineList;
                if (checked) {
                    if (response.data && Array.isArray(response.data)) {
                        response.data.map(id => declineList.push(id));
                    } else if (response.data && typeof response.data === 'number') {
                        declineList.push(response.data);
                    }
                } else {
                    if (response.data && Array.isArray(response.data)) {
                        response.data.map(id => {
                            let index = declineList.indexOf(id);
                            if (index > -1) {
                                declineList.splice(index, 1);
                            }
                        });
                    } else if (response.data && typeof response.data === 'number') {
                        let index = declineList.indexOf(response.data);
                        if (index > -1) {
                            declineList.splice(index, 1);
                        }
                    }
                }
                declineList = Array.from(new Set(declineList));
                self.setState({declineList});
                self.props.changeOnDeclineList(declineList);
                self.props.loading(false);
            }).catch(function (error) {
            showAxiosError(error);
            self.props.loading(false);
        });
    }

    render() {
        let maxLength = this.state.maxLength ? this.state.maxLength : 0;
        const icon = ({}) => (<i className="fa fa-university"/>);
        const loop = (nodes, width) => {
            if (!nodes || !nodes.length) {
                return null
            }
            return nodes.map((node) =>
                <TreeNode key={node.key}
                          title={customLabel(node, width)}>
                    {loop(node.children, width - 18)}
                </TreeNode>
            )
        };
        const customLabel = (node, width) => (

            <div style={{display: 'inline-flex'}}>

                <Col xs='10' style={{margin: '0', padding: '0', width: width + "px",}} title={node.title}>
                    <label style={{overflow: 'hidden'}}> {node.title}... </label>
                </Col>

                <div className='2' style={{display: 'inline-flex'}}>
                    <Col xs={'6'}>
                        |
                        <input type="checkbox" style={{width: '40px'}}
                               checked={this.state.selectedDepts && this.state.selectedDepts[node.key]}
                               onChange={e => this.onSelectionChange(node, e)}/>
                        |
                    </Col>

                    {/*<Col xs={'6'}>*/}
                    {/*    |*/}
                    {/*    <input type="checkbox" style={{width: '40px'}}*/}
                    {/*           checked={this.state.conditionalList && this.state.conditionalList[node.title]}*/}
                    {/*           onChange={e => this.conditionalListChange(node, e)}/>*/}
                    {/*    |*/}
                    {/*</Col>*/}
                </div>
            </div>

        );

        return (
            <div className="col-xs-12">
                <div style={{marginBottom: '-15px'}}>
                    <label style={{width: (maxLength + 135) + "px", textAlign: 'center'}}>{this.props.selectedAuthorizationWay===authorizationWays.EXPENDITURE?"Masraf Yeri":"Departman"}</label>
                    <label style={{width: "40px", textAlign: 'center'}}>Seçim</label>
                    {/*<label style={{width: "120px", textAlign: 'center'}}>Şartlı Onay</label>*/}
                </div>
                <div>
                    <Tree showLine
                          icon={icon}
                          selectable={false}
                          defaultExpandAll={true}>
                        {loop(this.props.treeData, maxLength)}
                    </Tree>
                </div>
            </div>
        )
    }
}

export default DepartmentSelectionTree;